import React, { useState, useEffect } from 'react';
import { Autocomplete, Button, Checkbox, Chip, Drawer, ToggleButton, ToggleButtonGroup, Grid, IconButton, List, ListItem, ListItemButton, ListItemText, Stack, TextField, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'
import CloseIcon from "@mui/icons-material/Close";

import { isAnyMatch } from '../utils/commonUtils';
import { mainBackgroundColor, toggleButtonStyle } from '../utils/commonStyle';

import { Img, ImgOffset } from './Img';






const icon = [
  'r', 'b', 'g', 'y', 'w', 'p',
  '', '', '', '', '', '', '',
  'create-s','create-b','create-r','create-m','fast-s','fast-b','fast-r','pc','pb','gaugeUp',
  'box','nuts','mouse','wood','chain','gift','jelly','wine','button','orange','cobweb','bubble','ice','printer','','','','1','2','Wave+1',
]
const textMap = {
  'main1': '1',
}
export const filterList = {
  card: {
    get: ['', '', '', '', '', ''],
    rarity: [1, 2, 3, 4],
    color: ['r', 'b', 'g', 'y', 'w', 'p'],
    charName: ['', '', '', '', '', '', ''],
    skill:['create-s','create-b','create-r','create-m','fast-s','fast-b','fast-r','pc','pb','gaugeUp'],
    ability:['box','nuts','mouse','wood','chain','gift','jelly','wine','button','orange','cobweb','bubble','ice','printer','','','','1','2','Wave+1',]
  },
}
export const filterItemitialize = (category) => {
  return ({
    ...Object.fromEntries(Object.entries(filterList[category]).map(([key]) => [key, []])),
  })
}
const groupTitle = (category) => ({
  get: '',
  rarity: '',
  color:'',
  charName:'',
  skill:'',
  ability:'',
  got: '',
});


const StarIconGroup = ({ count, selected }) => {
  return (
    [...Array(Number(count))].map((_, starIndex) => (
      <StarIcon
        key={starIndex}
        style={{
          // color: selected ? checkColor : 'yellow',
          fontSize: '16px',
          marginRight: -3
        }}
      />
    ))
  );
};





const containerStyle = {//
  display: 'flex',
  flexWrap: 'wrap',
};
const toggleButtonGroupStyle = {
  pb: 0.5,
  // height: 36,
  padding: 0,
  display: 'flex',
  flexWrap: 'wrap',
  // padding: '8px 16px', // 
  //  min-height 
  minHeight: '36px', // 
};
export const FilterButtonGroup = ({ category, filterItems, setFilterItems, group }) => {

  //filterItems[group]localStrage
  useEffect(() => {
    if (filterItems[group] === undefined) {
      filterItems[group] = []
      setFilterItems(prevFilterItems => ({ ...prevFilterItems, [group]: [] }));
    }
  }, []);

  const [items, setItems] = React.useState(filterItems[group] ?? []);

  const updateSelectedFilterItems = (event, newItems) => {
    setItems(newItems);
    const updatedFilterItems = {
      ...filterItems,
      [group]: [...newItems]
    }
    setFilterItems(updatedFilterItems)
    localStorage.setItem('shm-filterItems-' + category, JSON.stringify(updatedFilterItems));
  };

  const renderButton = (name, index) => {
    const isNumber = !isNaN(name);
    const isIcon = icon.includes(name);
    const text = textMap[name]
    const selected = items.includes(name);


    const renderContent = (name, selected, group) => {
      if (isNumber) {
        return <StarIconGroup count={name} selected={selected} />;
      }

      if (isIcon) {
        if (group === 'charName') {
          return <div style={{ width: 40 }}>
            <ImgOffset
              name={name}
              width={40}
            />
          </div>;
        }
        return <Img
          name={name}
          width={group==='color' ? 26 : 30}
        />;
      }

      if (text) {
        return text;
      }




      return name;
    }

    return (
      <ToggleButton
        value={name}
        aria-label={name}
        key={index}
        size="small"
        sx={toggleButtonStyle}
        selected={selected}
      >
        {renderContent(name, selected, group)}
      </ToggleButton >
    );
  };

  return (
    <>
      <div style={{ fontSize: '12px', lineHeight: 1 }}>
        {groupTitle(category)[group] ?? group}
      </div>
      <div style={containerStyle}>
        <ToggleButtonGroup
          value={items}
          onChange={updateSelectedFilterItems}
          aria-label={'filterButton'}
          sx={toggleButtonGroupStyle}
        >
          {filterList[category][group].map(renderButton)}
        </ToggleButtonGroup>
      </div>
    </>
  )
}




const filterPassFunctionMap = () => ({
  card: (el, filterItems) => (
    (filterItems.get.length === 0 || filterItems.get.includes(el.get)) &&
    (filterItems.rarity.length === 0 || filterItems.rarity.includes(el.rarity)) &&
    (filterItems.color.length === 0 || filterItems.color.includes(el.color)) &&
    (filterItems.charName.length === 0 || filterItems.charName.includes(el.charName)) &&
    (filterItems.skill.length === 0 || isAnyMatch(filterItems.skill,Object.keys(el))) &&
    (filterItems.ability.length === 0 || isAnyMatch(filterItems.ability,Object.keys(el)))
  ),
});
export function isFilterPassed({ category, el, filterItems }) {
  // console.log(el.name)
  const filterPassFunctions = filterPassFunctionMap();
  const filterPassFunction = filterPassFunctions[category];

  if (!filterPassFunction) console.log(category);
  return filterPassFunction(el, filterItems);
}




















