import React from 'react';
import './App.css';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Button from '@mui/material/Button';

import { darken, lighten } from '@mui/system';


import MainPage from './components/MainPage';

export const mainBackgroundColor = '#F9EFE0'
// export const selectedBackgroundColor = '#EBE6DC'
export const checkedColor = '#D39749'
export const mainColor = '#724032'


// 
const theme = createTheme({
  palette: {
    primary: {
      main: '#d2b48c', // 
    },
    secondary: {
      main: '#f5f5dc', // 
    },
  },
  typography: {
    // 
    allVariants: {
      color: darken(mainColor, 0.6), // 
    },
  },
  components: {
    MuiToggleButtonGroup: {
      styleOverrides: {
        grouped: {
          '&:not(:first-of-type)': {
            borderLeftColor: darken(mainColor, 0.5),
          },
        },
      },
    },
    MuiToggleButton: {
      styleOverrides: {
        root: {
          color: darken(mainColor, 0.6),
          backgroundColor: lighten(checkedColor, 0.7),
          borderColor: darken(mainColor, 0.6),

          '&:hover': {
            backgroundColor: lighten(checkedColor, 0.6),
          },
          '&.Mui-selected': {
            backgroundColor: lighten(checkedColor, 0.1),
            '&:hover': {
              backgroundColor: lighten(checkedColor, 0),
            },
          },
        },
      },
    },
    MuiIconButton: {
      styleOverrides: {
        root: {
          color: darken(mainColor, 0.6),
          backgroundColor: lighten(checkedColor, 0.1),
          borderColor: darken(mainColor, 0.6),

          '&:hover': {
            backgroundColor: lighten(checkedColor, 0),
            // 
          },
        },
      },
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <div className="App"
        style={{
          minHeight: '100vh',
          color:darken(checkedColor, 0.6),
          backgroundColor: darken(mainBackgroundColor, 0),
        }}>
        <MainPage />
      </div>
    </ThemeProvider>
  );
}

export default App;