import React, { useState, useEffect, useMemo } from 'react';
import { ButtonBase, Box, Chip, Collapse, Grid, List, ListSubheader, ListItem, ListItemButton, Stack, Tooltip, paperClasses } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'


import card from '../constants/card';
import skill from '../constants/skill';
import ability, { gimmicks } from '../constants/ability';
import { textMap } from '../utils/textMap';
import { Img } from './Img';
import { Scores, SADetail } from './Parts';
import { filterList, filterItemitialize, FilterButtonGroup, isFilterPassed, DrawerForSelect } from './Filter'
import { SortOrderButton, SortButtonGroup, sortItemList } from './Sort'
import { RowContents } from './RowContents'
import { mainColor, checkedColor, mainBackgroundColor } from '../App';

const category = 'card'
export const maxValues = {
  score : 0
}

function customFunction(x) {
  if (x === 3) {
    return 5;
  } else if (x === 4) {
    return 10;
  } else {
    return 0; // 
  }
}
function findSkillCostReduction(array) {
  for (let i = 0; i < array.length; i++) {
    if (array[i] === '5') {
      return 5;
    } else if (array[i] === '10') {
      return 10;
    } else if (array[i] === '15') {
      return 15;
    }
  }
  return 0; // 
}
Object.keys(card).forEach(charName => {
  Object.entries(card[charName]).forEach(([cardTitle, el]) => {
    el.name = charName + '' + cardTitle + ''
    el.charName = charName
    el.cardTitle = cardTitle
    el.skillCost = skill[el.skill].cost - customFunction(el.rarity) - findSkillCostReduction(el.abilities)

    //skill
    if( el.skill.includes('create')){
      const boosters = el.skill.split('-')[1]
      if( boosters.includes('s')){
        el['create-s'] = true
      }
      if( boosters.includes('b')){
        el['create-b'] = true
      }
      if( boosters.includes('r')){
        el['create-r'] = true
      }
      if( boosters.includes('m')){
        el['create-m'] = true
      }
      
    }else if(el.skill.includes('fast')){
      const boosters = el.skill.split('-')[1].split('-')[0]
      if( boosters.includes('s')){
        el['fast-s'] = true
      }else if(boosters.includes('b') ){
        el['fast-b'] = true
      }else if(boosters.includes('r') ){
        el['fast-r'] = true
      }else{
        console.log('fast',el.skill)
      }

    }else if ( el.skill === 'pc'){
      el.pc = true
    }else if ( el.skill === 'pb'){
      el.pb = true
    }else if (el.skill.includes('gaugeUp')){
      el.gaugeUp = true
    }else{
      console.log('skill',el.skill)
    }




    el.abilities.forEach(aKey => {
      if (aKey.includes('scoreUp')) {
      } else if (Object.keys(gimmicks).includes(aKey.split('-')[0])) {
        el[aKey.split('-')[0]] = true
      } else {
        el[ability[aKey].img] = true
      }
    })

    //maxValue
    el.scores.forEach(v=>{
      if( v >  maxValues.score){
        maxValues.score = v
      }
    })

  })
})

const MainPage = () => {


  const [sortOrder, setSortOrder] = useState(() => {
    return localStorage.getItem('shm-sortOrder-' + category) || 'desc';
  });
  const [sortItem, setSortItem] = useState(() => {
    return localStorage.getItem('shm-sortItem-' + category) || 'date';
  });
  const [filterItems, setFilterItems] = useState(() => {//category
    const storedData = localStorage.getItem('shm-filterItems-' + category);
    return storedData ? { ...filterItemitialize(category), ...JSON.parse(storedData) } : filterItemitialize(category);
  });


  const rows = useMemo(() => {
    let newRows = [];
    Object.keys(card).forEach(charName => {
      Object.entries(card[charName]).forEach(([cardTitle, el]) => {
        if (isFilterPassed({ category, el, filterItems })) {


          newRows.push({
            ...el,
            r: el.scores[0],
            b: el.scores[1],
            g: el.scores[2],
            y: el.scores[3],
            w: el.scores[4],
            p: el.scores[5],
            totalScore: el.scores[0] + el.scores[1] + el.scores[2] + el.scores[3] + el.scores[4] + el.scores[5]

          })
        }
      });


    })


    newRows.sort((a, b) => {
      if (a[sortItem] === undefined) return 1;
      if (b[sortItem] === undefined) return -1;

      const itemA = sortItem === 'date' ? new Date(a[sortItem]) : a[sortItem];
      const itemB = sortItem === 'date' ? new Date(b[sortItem]) : b[sortItem];
      if (sortOrder === 'asc') {
        if (itemA < itemB) return -1;
        if (itemA > itemB) return 1;
      } else {
        if (itemA < itemB) return 1;
        if (itemA > itemB) return -1;
      }
      return 0;
    });

    // console.log(newRows)

    return newRows;
  }, [filterItems, sortItem, sortOrder,]);// trigger








  return (
    <div>
      <Grid container>

        <Grid item style={{
          display: 'inline-block', //  'inline-flex'
          backgroundColor: lighten(checkedColor, 0.6),
          minWidth: 420,
          // marginTop: '4px',
          paddingRight: '4px',
        }}>
          <div
            style={{
              fontSize: '16px',
              fontWeight: 'bold',
              paddingLeft: '4px',
              lineHeight: 1.2,
            }}>
            
          </div>
          <Grid container>
            <Grid item sx={{ pt: '4px', pl: '4px' }}>
              <SortOrderButton category={category} sortOrder={sortOrder} setSortOrder={setSortOrder} />
            </Grid>
            <Grid item sx={{ pt: '4px', pl: '4px' }}>
              <SortButtonGroup props={{ category, sortItem, setSortItem, sortItemList: sortItemList[category] }} />
            </Grid>
          </Grid>
        </Grid>

        <Grid sx={{
          display: 'inline-block', //  'inline-flex'
          backgroundColor: lighten(checkedColor, 0.6),
          marginTop: '4px',
          paddingRight: '4px',
          paddingBottom: '4px',
          minWidth: 420,
        }}>
          <div
            style={{
              fontSize: '16px',
              fontWeight: 'bold',
              paddingLeft: '4px',
              lineHeight: 1.2,
            }}>
            
          </div>
          <Grid container>
            {Object.keys(filterList[category]).map(group => {

              if (!group.includes('effectTarget')) {
                return (
                  <Grid item key={group} sx={{ pl: '4px', pt: '4px' }}>
                    <FilterButtonGroup
                      category={category}
                      filterItems={filterItems}
                      setFilterItems={setFilterItems}
                      group={group}
                    />
                  </Grid>
                );
              }
              return null;
            })}
          </Grid>
        </Grid>

        <Grid item sx={{ marginTop: '4px' }}>
          <RowContents
            rows={rows}



          />
        </Grid>






      </Grid>
    </div>

  )
}
export default MainPage