export const gimmicks = {
  'box': '',       // '' 'cardboard'
  'nuts': '',          // '' 'peanut'
  'mouse': '',               // '' 'mouse'
  'wood': '',               // '' 'firewood'
  'chain': '',                   // '' 'chain'
  'gift': '',       // '' 'gift box' 'giftBox'
  'jelly': '',// '' 'jelly'
  'wine': '',                // '' 'wine'
  'button': '',              // '' 'button'
  'orange': '',       // '''mandarin box'
  'cobweb': '',            // '' 'cobweb'
  'bubble': '',                  // '' 'bubble'
  'ice': '',                     // '' 'ice'
  'printer': '3D',     // '3D' '3D printer' '3DPrinter'
}

const numbers = [1, 2]

const boosters = {
  b: '',
  r: '',
  m: '',
  s: '',
}




Object.entries(gimmicks).forEach(([gKey, gName]) => {
  Object.entries(boosters).forEach(([bKey, bName]) => {
    numbers.forEach(n => {
      ability[gKey + '-' + n + '-' + bKey] = {
        name: gName + '' + '+' + n + '(' + bName + ')',
        text: bName + '' + gName + '' + n + '\n()',
        img: gKey + '-' + bKey,
        killer: gName,
        value: n,
      }
    })
  });
});


const colors = {
  'r': '',
  'b': '',
  'g': '',
  'y': '',
  'w': '',
  'p': '',
}
const targets = {
  'c': '',
  'd': '',
}

const values = {
  'c': [5, 10, 20],
  'd': [3, 5, 10],
}

Object.entries(colors).forEach(([cKey, cName]) => {
  Object.entries(targets).forEach(([tKey, tName]) => {
    Object.values(values[tKey]).forEach(v => {

      ability['scoreUp-' + tKey + '-' + v + '-' + cKey] = {
        name: tName + '' +v+'%(' + cName + ')',
        text: tKey === 'c'
        ? '' + cName + ''+ v +'%\n(%)'
        : '' + cName + ''+ v +'%\n(%)',


        img: 'scoreUp' + '-' + tKey + '-' + cKey,
      }
    })
  })
})

