const colorCodes = [
  '#ff0000',
  '#007fff',
  '#00ff00',
  '#ff7f00',
  '#00ffff',
  '#ff00ff',
]



export const Scores = ({ scores }) => {

  return (
    <div style={{ display: 'flex', flexDirection: 'row' }}>
      {scores.map((v, index) => (
        <div key={index}
          style={{
            boxSizing: 'border-box', /* borderpadding */
            width: 60,
            height: 20,
            position: 'relative',
            textAlign: 'right',
            border: `1px solid ${mainColor}`,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'flex-end',
          }}
        >
          <div style={{
            position: 'absolute',
            top: 0,
            left: 0,
            height: '100%',
            width: v / maxValues.score * (60 - 2),
            // backgroundColor: isSortItem ? darken(checkColor, 0.2) : lighten(mainBackgroundColor, 0.2)
            // backgroundColor: colorCodes[index],
            backgroundColor: lighten( colorCodes[index] ,0.6) ,
          }}
          ></div>

          <div

            style={{
              display: 'flex',
              justifyContent: 'flex-end',
              alignItems: 'center',
              paddingRight: '2px',
              fontSize: '14px',
              zIndex:1,
            }}
          >
            {v.toLocaleString()}
          </div>
        </div>
      ))}
    </div>
  )
}




export const SAIcon = ({ el, cost }) => {

  const value = el.value ?? 0

  return (
    <div style={{ position: 'relative', width: 50 }}>
      <div style={{ height: 60 }}>

        <Img name={el.img} square={50} />

        {cost && (
          <div style={{
            position: 'absolute',
            width: '100%',
            bottom: 0, // Img
            left: '50%', // 50%
            transform: 'translateX(-50%)', // 
            color: 'white',
            // backgroundColor: 'rgba(224, 182, 92, 100)', // 50%
            backgroundColor: 'rgba(128, 64, 0, 0.5)', // 50%
            fontWeight: 'bold',
            lineHeight: 1,
            textAlign: 'center', // 
            borderRadius: '10px', // 
          }}>
            {cost}
          </div>
        )}

        {value >= 2 && (
          <div style={{
            position: 'absolute',
            top: 0,
            right: 0,
            color: 'white',
            // backgroundColor: 'rgba(224, 182, 92, 100)', // 50%
            backgroundColor: 'rgba(0, 0, 0, 0.5)', // 50%
            fontWeight: 'bold',
            lineHeight: 1,
            textAlign: 'center', // 
            borderRadius: '10px', // 
            paddingLeft: '4px',
            paddingRight: '4px',
          }}>
            {value}
          </div>
        )}


      </div>
    </div>
  )

}

