import React, { useMemo } from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';






export const TextBr = ({ text }) => {
  if (!text) {
    return null;
  }

  const lines = text.split('\n').map((line, index) => (
    <React.Fragment key={index}>
      {line}
      <br />
    </React.Fragment>
  ));

  return <>{lines}</>;
};




