import React, { useState, useEffect } from 'react';
import { ButtonBase, Box, Chip, Collapse, Grid, List, ListSubheader, ListItem, ListItemButton, Stack, Tooltip } from '@mui/material';
import { darken, lighten } from '@mui/system';
import StarIcon from '@mui/icons-material/Star'

import { mainColor, checkedColor, mainBackgroundColor } from '../App';
import skill from '../constants/skill';
import ability from '../constants/ability';
import { textMap } from '../utils/textMap';
import { Img } from './Img';
import { Scores, SAIcon, SADetail } from './Parts';


export const RowContents = ({ rows }) => {

  const [selectedName, setSelectedName] = useState(null);
  const handleRowClick = (name) => {
    setSelectedName(name === selectedName ? null : name);
  };



  return (
    <Grid container direction="column">
      {rows.map(el => {
        return (
          <Grid item key={el.name}
            sx={{
              boxShadow: selectedName === el.name
                ? `0 0 0 2px limegreen`
                : 'none', // 
              '&:hover': {
                bgcolor: lighten(checkedColor, 0.7),
              },
              margin: '2px',
            }}
            onClick={() => handleRowClick(el.name)}
          >
            <Grid container
              sx={{
                border: 2,
                borderColor: darken(mainColor, 0),
              }}
            >
              <Grid item sx={{ width: 420, marginRight: '4px' }}>
                <Box display="flex" flexDirection="row" alignItems="center">
                  <div style={{ height: 60 }}>
                    <Img name={el.name} square={60} />
                  </div>
                  <div>
                    <Stack sx={{ height: 40, fontWeight: 'bold' }}>
                      <div style={{ display: 'frex', height: 20, paddingLeft: '4px' }}>
                        <Img name={el.color} square={20} />
                        {[...Array(el.rarity)].map((_, index) => (
                          <StarIcon key={index}
                            style={{
                              // color: darken('#ffff00', 0.1),
                              fontSize: '20px',
                              marginRight: -4,
                            }}
                          />
                        ))}

                      </div>
                      <div style={{ display: 'flex', justifyContent: 'space-between', paddingLeft: '4px', lineHeight: 1.2 }}>
                        <span>{''+ el.cardTitle+''}</span>
                        <span>{el.totalScore.toLocaleString()}</span>
                      </div>

                    </Stack>
                    <Scores scores={el.scores} />
                  </div>
                </Box>
              </Grid>
              <Grid item sx={{ width: 420 }}>

                <Collapse in={selectedName !== el.name}>
                  <Grid container>
                    <Grid item>
                      <SAIcon el={skill[el.skill]} cost={el.skillCost}/>
                    </Grid>
                    {el.abilities.map((aKey, index) => {
                      // console.log(data[aKey] , aKey)
                      if( aKey.includes('scoreUp')) return <React.Fragment  key={'i'+index}></React.Fragment>
                      return (
                        <Grid item key={'i'+index}>
                          <SAIcon el={ability[aKey]} />

                        </Grid>
                      )
                    })}

                  </Grid>
                </Collapse>

                <Collapse in={selectedName === el.name}>
                  <SADetail el={skill[el.skill]}  cost={el.skillCost}/>
                  {el.abilities.map((aKey, index) => {
                    // console.log(data[aKey] , aKey)
                    return (
                      <SADetail key={'d'+index} el={ability[aKey]} />
                    )
                  })}
                </Collapse>
              </Grid>


            </Grid>
          </Grid>


        )
      })

      }




    </Grid>

  )
}